import styled from "styled-components";
const ResetStyles = styled.div`
figure {
  margin: 0;
}
img.responsive {
  display: block;
}
div[class*= padding] >: last - child {
  margin - bottom: 0;
}
nav > ol, nav > ul {
  list - style: none;
  padding: 0;
  margin: 0;
}
nav > ol li, nav > ul li {
  margin: 0;
  padding: 0;
}

ul {
  margin-top: 0;
  margin-bottom: 1.6em;

}
ul li {
  line - height: 1.75em;
}
  .as - h1, .as - h2, .as - h3, .as - h4, .as - h5, .as - h6, h1, h2, h3, h4, h5, h6, p {
  margin - top: 0;
}
p {
  margin - bottom: 1.6em;
  line - height: 1.75em;
}
  .as - h1, .as - h2, .as - h3, .as - h4, .as - h5, .as - h6, h1, h2, h3, h4, h5, h6 {
  line - height: 1.2em;
  margin - bottom: 1.2em;
  position: relative;
}
  .as - h1, h1 {
  font - size: 2.25em;
}
  .as - h2, h2 {
  font - size: 2em;
}
  .as - h3, h3 {
  font - size: 1.625em;
}
  .as - h4, h4 {
  font - size: 1.375em;
}
  .as - h5, h5 {
  font - size: 1.125em;
}
  .as - h6, h6 {
  font - size: .875em;
}
  }@media(min - width: 768px) {
      .as - h1, h1 {
    font - size: 2.5em;
  }
  .as - h2, h2 {
    font - size: 2em;
  }
  .as - h3, h3 {
    font - size: 1.625em;
  }
  .as - h4, h4 {
    font - size: 1.375em;
  }
  .as - h5, h5 {
    font - size: 1.125em;
  }
  .as - h6, h6 {
    font - size: .875em;
  }
} @media(min - width: 992px) {
      .as - h1, h1 {
    font - size: 3em;
  }
  .as - h2, h2 {
    font - size: 2.5em;
  }
  .as - h3, h3 {
    font - size: 2em;
  }
  .as - h4, h4 {
    font - size: 1.5em;
  }
  .as - h5, h5 {
    font - size: 1.25em;
  }
  .as - h6, h6 {
    font - size: .875em;
  }
} @media(min - width: 1200px) {
      .as - h1, h1 {
    font - size: 3.75em;
  }
  .as - h2, h2 {
    font - size: 3em;
  }
  .as - h3, h3 {
    font - size: 2.25em;
  }
  .as - h4, h4 {
    font - size: 1.75em;
  }
  .as - h5, h5 {
    font - size: 1.375em;
  }
  .as - h6, h6 {
    font - size: .875em;
  }
}
html {
  scroll - behavior: smooth;
}`;

export default ResetStyles;
