import { gql } from "apollo-boost";
export const GetAboutUsQuery = gql`
  {
    staticContent(where: { id: "ck5ncesq7ymwu0b20l2l59qa9" }) {
      status
      updatedAt
      createdAt
      id
      content {
        raw
        html
        text
      }
      title
      uri
    }
  }
`;

export const GetLandingPageQuery = gql`
  {
    staticContent(where: { id: "ck5u3t1ebbpik0b663h37oscp" }) {
      status
      updatedAt
      createdAt
      id
      content {
        raw
        html
        text
      }
      title
      uri
    }
  }
`;
export const GetPortfolioQuery = gql`
  {
    portfolioItems(orderBy: order_ASC) {
      status
      updatedAt
      createdAt
      id
      portfolioKey
      imageUrl {
        status
        updatedAt
        createdAt
        id
        handle
        fileName
        height
        width
        size
        mimeType
      }
      title
      subtitle
      content(orderBy: order_ASC) {
        status
        body {
          text
          html
        }
        updatedAt
        createdAt
        id
        heading
        imageAfter {
          status
          updatedAt
          createdAt
          id
          handle
          fileName
          height
          width
          size
          mimeType
        }
        subheading
        bgHex {
          hex
          css
          rgba {
            r
            g
            b
            a
          }
        }
        textColor {
          hex
          css
          rgba {
            r
            g
            b
            a
          }
        }
        imageBefore {
          status
          updatedAt
          createdAt
          id
          handle
          fileName
          height
          width
          size
          mimeType
        }
      }
      order
    }
  }
`;
