import React, { useState, useRef, useEffect } from "react";
import ReactHtmlParser from "react-html-parser";

import { Link, animateScroll as scroll } from "react-scroll";
import { Element } from "react-scroll";
import { PortfolioItemWrapper } from "./PortfolioSection.styles";
import { scrollToRef } from "./PortfolioSection";
import { interpolate, useTrail, animated } from "react-spring";

import { IPortfolioObject } from "../../data/PortfolioData";

interface IPortfolioItemProps {
  item: IPortfolioObject;
  onDetailsClick: (key: string) => void;
  onNextClick: (key: string) => void;
  onDetailsClose: () => void;
  executeScroll: () => void;
  currentNavItemKey: string;
  nextKey: string;
}
const PortfolioItem = (props: IPortfolioItemProps) => {
  const [isItemActive, setIsItemActive] = useState(false);
  const [isContentActive, setContentIsActive] = useState(false);
  const [swapTextLocation, setSwapTextLocation] = useState(false);
  const {
    item,
    onDetailsClick,
    onDetailsClose,
    currentNavItemKey,
    nextKey,
    onNextClick
  } = props;
  const { portfolioKey, title, imageUrl, subtitle, summary, content } = item;
  const config = { mass: 5, tension: 2000, friction: 200 };
  useEffect(() => {
    if (currentNavItemKey === portfolioKey && !isItemActive) {
    }
    return function cleanup() {};
  }, [currentNavItemKey]);
  const imageHeaderItems = [
    () => {
      return <h5>{title}</h5>;
    },
    () => {
      return <h6>{subtitle}</h6>;
    },
    () => {
      return (
        <Link to={portfolioKey} smooth={true}>
          <span
            id={`readMore${portfolioKey}`}
            className="read-more-button"
            onClick={() => {
              onReadMoreClicked();
            }}
          >
            Read More
          </span>
        </Link>
      );
    }
  ];
  const headerItems = [
    () => {
      return <h2>{title}</h2>;
    },
    () => {
      return <h6>{subtitle}</h6>;
    }
  ];
  const headerTrail = useTrail(imageHeaderItems.length, {
    config,
    opacity: !swapTextLocation ? 1 : 0,
    x: !swapTextLocation ? 0 : 20,
    height: !swapTextLocation ? 30 : 0,
    from: { opacity: 0, x: 20, height: 0 }
  });
  const contentHeaderTrail = useTrail(headerItems.length, {
    config,
    opacity: swapTextLocation ? 1 : 0,
    x: swapTextLocation ? 0 : 20,
    height: swapTextLocation ? 40 : 0,
    from: { opacity: 0, x: 20, height: 0 }
  });

  /**TODO the ref is currently not working because of a rerended on setcontentisactive */
  const onReadMoreClicked = () => {
    setIsItemActive(true);
    setTimeout(() => {
      setContentIsActive(true);
      setSwapTextLocation(true);
    }, 1000);
    setTimeout(() => {
      onDetailsClick(portfolioKey);
      scrollToRef(itemRef);
    }, 1300);
  };

  const onCloseClicked = () => {
    scrollToRef(itemRef);
    setContentIsActive(false);
    setSwapTextLocation(false);
    setTimeout(() => {
      onDetailsClose();
      scrollToRef(itemRef);
    }, 600);
    setTimeout(() => {
      setIsItemActive(false);
    }, 1300);
  };
  const itemRef = useRef(null);
  const backgroundImageStyles = {
    backgroundImage: `url(https://media.graphcms.com/${imageUrl.handle})`
  };
  const htmlDecode = input => {
    var e = document.createElement("div");
    e.innerHTML = input;
    return e.childNodes.length === 0 ? "" : e.childNodes[0].nodeValue;
  };
  const renderContent = contentItem => {
    const {
      body,
      imageUrl,
      imageAfter,
      id,
      heading,
      subheading,
      bgHex,
      textColor
    } = contentItem;
    return (
      <>
        {imageUrl && (
          <div className="contentImage">
            <img src={`https://media.graphcms.com/${imageUrl.handle}`} />
          </div>
        )}
        {body && body.html && (
          <div
            key={id}
            className="contentSection"
            style={{
              background: bgHex ? bgHex.hex : "#fff",
              color: textColor ? textColor.hex : "#000"
            }}
          >
            {heading && <h3>{heading}</h3>}
            {subheading && <h5>{subheading}</h5>}
            {body && (
              <div className="contentBody"> {ReactHtmlParser(body.html)} </div>
            )}
          </div>
        )}
        {imageAfter && (
          <div className="contentImage">
            <img src={`https://media.graphcms.com/${imageAfter.handle}`} />
          </div>
        )}
      </>
    );
  };

  return (
    <Element
      name={portfolioKey}
      className={isItemActive ? "activePortfolioItem" : "inactivePortfolioItem"}
    >
      <PortfolioItemWrapper ref={itemRef}>
        {isItemActive && (
          <div className="stickyWrapper">
            <div
              id={`closeItem${portfolioKey}`}
              className={`icon social fb`}
              onClick={onCloseClicked}
            >
              <i className="fa fa-times"></i>
            </div>
          </div>
        )}

        <section>
          <div
            className={`portfolio-image ${
              isContentActive ? "smaller-image" : ""
            }`}
            style={backgroundImageStyles}
          >
            <div className="portfolio-header">
              <div>
                {headerTrail.map(({ x, height, ...rest }, index) => (
                  <animated.div
                    key={`titleTrail${portfolioKey}${index}`}
                    className="trails-text"
                    style={{
                      ...rest,
                      transform: interpolate(
                        [x],
                        x => `translate3d(0,${x}px,0)`
                      )
                    }}
                  >
                    <animated.div style={{ height }}>
                      {imageHeaderItems[index]()}
                    </animated.div>
                  </animated.div>
                ))}
              </div>
              <div>
                {contentHeaderTrail.map(({ x, height, ...rest }, index) => (
                  <animated.div
                    key={`contentTitleTrail${portfolioKey}${index}`}
                    className="trails-text"
                    style={{
                      ...rest,
                      transform: interpolate(
                        [x],
                        x => `translate3d(0,${x}px,0)`
                      )
                    }}
                  >
                    <animated.div style={{ height }}>
                      {headerItems[index]()}
                    </animated.div>
                  </animated.div>
                ))}
              </div>
            </div>
          </div>
          <div
            className="portfolio-content"
            style={
              isContentActive
                ? { height: "auto", overflow: "visible" }
                : { height: 0, overflow: "hidden" }
            }
          >
            {summary}
            {content.map(contentItem => {
              return renderContent(contentItem);
            })}
            {nextKey && (
              <div
                className="nextItem"
                onClick={() => {
                  onNextClick(nextKey);
                }}
              >
                View Next
              </div>
            )}
          </div>
        </section>
      </PortfolioItemWrapper>
    </Element>
  );
};
export default PortfolioItem;
