import { Link } from "react-scroll";
import React from "react";
import { NavigationWrapper } from "./Navigation.styles";

const Navigation = props => {
  const { location, onLinkClicked } = props;

  return (
    <NavigationWrapper className={location && location}>
      <li>
        <Link
          to="aboutSection"
          spy={true}
          smooth={true}
          duration={500}
          onClick={() => {
            onLinkClicked();
          }}
        >
          About
        </Link>
      </li>
      <li>
        <Link
          to="portfolioSection"
          spy={true}
          smooth={true}
          duration={500}
          onClick={() => {
            onLinkClicked();
          }}
        >
          Work
        </Link>
      </li>
      <li>
        <Link
          to="contactSection"
          spy={true}
          smooth={true}
          duration={500}
          onClick={() => {
            onLinkClicked();
          }}
        >
          Contact
        </Link>
      </li>
      <li>
        <a href="/JessicaSutherlandResume.pdf" target="_blank">
          Resume
        </a>
      </li>
    </NavigationWrapper>
  );
};

export default Navigation;
