import React from "react";
import { StickyNavWithHeaderWrapper } from "./Navigation.styles";
import Navigation from "./Navigation";
import { Element } from "react-scroll";
const StickyNavWithHeader = props => {
  const { color, title, sectionAnchor, renderSubmenu } = props;
  return (
    <StickyNavWithHeaderWrapper className={color}>
      <Element className="navWithHeader sticky" name={sectionAnchor}>
        <div className="navHeader">
          <h1>{title}</h1>
          {renderSubmenu}
        </div>
        <Navigation onLinkClicked={() => {}} />
      </Element>
    </StickyNavWithHeaderWrapper>
  );
};
export default StickyNavWithHeader;
