import React from "react";
import { ContactSectionWrapper } from "./ContactSection.styles";
import StickyNavWithHeader from "../Navigation/StickyNavWithHeader";
import ContactMeData from "../../data/ContactMeData";
const ContactSection = () => {
  return (
    <ContactSectionWrapper className="sectionWrapper">
      <StickyNavWithHeader
        color="darkNav"
        title="Contact"
        sectionAnchor="contactSection"
      />
      <div className=" page">
        <div className="contentWrapper">
          <ContactMeData />
        </div>
      </div>
    </ContactSectionWrapper>
  );
};
export default ContactSection;
