import styled from "styled-components";

const popColor1 = "#e0b312";
export const HeaderHero = styled.header`
  // grid-column: 1 / 2;
  // background: #eeeeee url("/JessicaWebsite.jpg") no-repeat;
  // background-size: cover;
  // background-position: bottom center;
  // background: #eee;
  color: #000;
  text-align: center;
  overflow:hidden;
  flex-direction:column;
  justify-content:center;
  flex:1;
  .homeTitle{
    padding: 0 50px;
    min-height: 100vh;
    // border-right: 1px solid #efefef;
  }
  // grid-template-columns: auto;
  // grid-template-rows: 25% auto;
  .mobileNavigation {
    display:none;
    opacity:0;
    transition: all 500ms;
    // display: none;
    position: fixed;
    left:0;
    top:0;
    z-index:101;
    height: 100vh;
    width: 100%;
    background: #333;
    align-items:center;
    text-align:center;
    justify-content:center;
    padding:30px;
    color:white;
    flex-direction:column;
    .headerNav{
      text-align:center;
      display:flex;
      flex-direction:column;
      margin:0;
      align-self:center;
      li{
        a{
          color:white;
        }
        line-height:1;
      }
      li:before{
        display:none;
      }
    }
  }
  .fixed-mobile-nav-trigger{
    display:none;
    z-index:100;
    font-size:20px;
    cursor:pointer;
    color:white;
    position:fixed;
    top:20px;
    left:20px;
    justify-content: center;
    align-items: center;
    background-color: ${popColor1};
    height: 40px;
    width: 40px;
    transition: all 500ms;
    border-radius: 100px;
    &.close-it{
      display:none;
      z-index:102;
      left:auto;
      right:20px;
      &.active{
        display:flex;
      }
    }
  }

  .homeTitle {
    display:flex;
    flex-direction:column;
    justify-content:center;
    text-align: left;
    // margin-top: 100px;
    // margin-left: 60px;
    color: black;
    span {
      display:block;
      // display: none;
      margin-left: 0px;
      font-size: 1.15vw;
      // letter-spacing:1px;
      font-weight: 400;
      padding:0 20px;
      line-height:1.2;
      color:#333;
    }
    h1 {
      font-family: "Dianna Webber", cursive;
      color:${popColor1}
      font-size: 6.95vw;
      margin-bottom:0px;
    }
  }

  .smallHeader {
    font-size: 16px;
    text-transform: uppercase;
    padding: 28px 0;
    span {
      margin: 0 10px;
    }
  }
  @media only screen and (max-width: 1360px) {
    .homeTitle{
      // margin-top:50px;
      // justify-content:flex-start
    }
  }
  @media only screen and (max-width: 980px) {
    background:#fff;
    .fixed-mobile-nav-trigger{
      display:flex;
    }
    .mobileNavigation.active {
      display:flex;
      opacity:1;
    }
    .homeTitle{
      margin:0;
      justify-content:center;
      // margin-top:0px !important;
      // padding:50px;
      text-align:center;
      display:flex;
      h1{ font-size:90px;}
      span{
        width:100%;
        align-self:center;
        font-size:16px;
        line-height:1.5;
        margin:0;
        padding:0;
      }
    }

  }
`;
