import styled from "styled-components";

const popColor1 = "#e0b312";
export const PortfolioSectionWrapper = styled.div`
  &.detailsMode {
    .inactivePortfolioItem {
      display: none;
    }
    nav {
      flex-grow: 0.6;
    }
  }
  .activePortfolioItem {
    section {
      height: auto;
      margin-top: -40px;
    }
    .portfolio-image {
      position: relative;
    }
  }
  .inactivePortfolioItem {
    section {
      overflow: hidden;
    }
  }
`;
export const PortfolioItemWrapper = styled.div`
  section {
    height: 100vh;
    position: relative;
  }
  .nextItem {
    padding: 30px;
    font-size: 30px;
    font-weight: bold;
    cursor: pointer;
    margin-bottom: 40px;
    text-align: center;
    &:hover {
      color: ${popColor1};
    }
  }
  .read-more-button {
    cursor: pointer;
    margin-top: 10px;
    padding: 5px 40px;
    display: inline-block;
    transition: 500ms all;
    cursor: pointer;
    &:hover {
      color: ${popColor1};
    }
  }
  .stickyWrapper {
    top: 100px;
    position: sticky;
    z-index: 10;
    display: flex;
    margin-left: -20px;
    flex-direction: row;
  }
  .icon {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: ${popColor1};
    height: 40px;
    width: 40px;
    transition: all 500ms;
    border-radius: 100px;
    cursor: pointer;
    transition: 0.5s;
    -moz-transition: 0.5s;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    text-align: center;
    color: #ffffff;
    &.active {
      opacity: 1;
    }
    &:hover {
      background-color: black;
    }
  }
  @media only screen and (max-width: 980px) {
    .stickyWrapper {
      top: 20px;
      justify-content: flex-end;
      margin-left: 0;
      margin-right: 20px;
      .icon {
        align-self: flex-end;
      }
    }
  }
  .icon i {
    font-size: 20px;
    color: white;
  }

  .portfolio-header {
    z-index: 2;
    text-align: center;
    color: white;
    h5 {
      margin-bottom: 0px;
      text-transform: uppercase;
    }
  }
  .portfolio-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-size: cover;
    background-position: center center;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 500ms;
    &:after {
      position: absolute;
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background: black;
      z-index: 1;
      opacity: 0.3;
    }
    &.smaller-image {
      height: 380px;
    }
  }
  .contentSection {
    padding: 40px 10%;
  }
  .contentImage {
    width: 100%;
    img {
      max-width: 100%;
    }
  }
`;
