import React from "react";
import { Link } from "react-scroll";
const PortfolioNav = props => {
  const { onNavItemClick, portfolioList } = props;
  const renderPortfolioNavItem = portfolioItem => {
    return (
      <li key={portfolioItem.portfolioKey}>
        <Link
          to={portfolioItem.portfolioKey}
          spy={true}
          smooth={true}
          duration={500}
          onClick={() => {
            onNavItemClick(portfolioItem.portfolioKey);
          }}
        >
          {portfolioItem.title}
        </Link>
      </li>
    );
  };
  return (
    <div className="nav-sub-header">
      <ul>
        {portfolioList.map(portfolioItem => {
          return renderPortfolioNavItem(portfolioItem);
        })}
      </ul>
    </div>
  );
};
export default PortfolioNav;
