import styled from "styled-components";

const popColor1 = "#e0b312";
export const NavigationWrapper = styled.ul`
  white-space: nowrap;
  padding: 40px 0px;
  text-align: center;
  list-style: none;
  margin: 0 0 1.6em 0;
  padding: 0;
  text-transform: uppercase;
  width: 100%;

  &.headerNav {
    width: auto;
  }
  a {
    font-weight: 300;
    font-size: 14px;
    color: black;
    cursor: pointer;
    text-decoration: none;
    &:hover,
    &.active {
      border-bottom: 2px solid ${popColor1};
      padding-bottom: 5px;
    }
  }
  li {
    display: inline-block;

    &:before {
      content: "|";
      padding: 0 12px 0 9px;
    }
    &:first-child:before {
      display: none;
    }
  }
  &.headerNav {
    text-align: left;
    margin-left: 20px;
    // display: none;
    margin-top: 60px;
    align-self: start;
    // align-self: end;
    color: #000;
    li:before {
      color: ${popColor1};
      font-weight: 100;
    }
    a {
      font-size: 18px;
      font-weight: 300;
    }
  }
  @media only screen and (max-width: 980px) {
    display: none;
    .homeTitle &.headerNav {
      display:block
      width: 100%;
      text-align: center;
      margin-left: 0;
      a {
        font-size: 14px;
      }
    }
  }
`;

export const StickyNavWithHeaderWrapper = styled.nav`
  .sticky {
    position: sticky;
    top: 0;
  }
  .navBottom {
    padding: 40px 0px;
    text-align: center;
    width: 100%;
    a {
      // font-weight: bold;
      font-size: 14px;
      font-weight: 300;
      color: black;
    }
  }
  .navWithHeader {
    padding: 0 22px;
    min-height: 100vh;
    display: grid;
    grid-template-rows: 1fr auto;
    grid-template-columns: 100%;
    border-right: 1px solid #efefef;
  }
  .navHeader {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    ul {
      padding: 0;
      text-align: center;
      li {
        font-size: 12px;
        display: inline-block;
        cursor: pointer;
        &:hover {
          color: ${popColor1};
        }
        a.active {
          font-weight: bold;
        }
      }
      li:first-child:before {
        display: none;
      }
      li:before {
        content: "|";
        padding: 0 6px 0 3px;
      }
    }
    h1 {
      font-family: "Dianna Webber", cursive;
      font-size: 120px;
      margin-bottom: 0;
      margin-top: 80px;
    }
  }
  @media only screen and (max-width: 980px) {
    .headerNav {
      margin-left: 0;
    }
    &.lightNav {
      background: #474644;
      color: white;
    }
    .navWithHeader {
      min-height: 250px;
    }
    .navHeader h1 {
      margin-top: 20px;
    }
  }
  &.darkNav {
    background: #474644;
    color: white;
    ul {
      li:before {
        color: ${popColor1};
      }
      a {
        color: white;
      }
    }
  }
`;
