import React from "react";
import { AboutSectionWrapper } from "./AboutSection.styles";
import StickyNavWithHeader from "../Navigation/StickyNavWithHeader";
import { GetAboutUsQuery } from "../../data/ApiService";
import { useQuery } from "@apollo/react-hooks";
const AboutSection = () => {
  const { loading, error, data } = useQuery(GetAboutUsQuery);
  const dataHtml = data ? data.staticContent.content.text : "";
  const htmlDecode = input => {
    var e = document.createElement("div");
    e.innerHTML = input;
    return e.childNodes.length === 0 ? "" : e.childNodes[0].nodeValue;
  };
  return (
    <AboutSectionWrapper className="sectionWrapper">
      <StickyNavWithHeader
        color="darkNav"
        title="About"
        sectionAnchor="aboutSection"
      />
      <div className=" page">
        <div className="contentWrapper">
          <div dangerouslySetInnerHTML={{ __html: dataHtml }} />
        </div>
      </div>
    </AboutSectionWrapper>
  );
};
export default AboutSection;
