import React, { useState, useRef } from "react";
import { PortfolioSectionWrapper } from "./PortfolioSection.styles";
import StickyNavWithHeader from "../Navigation/StickyNavWithHeader";
import PortfolioItem from "./PortfolioItem";
import PortfolioNav from "./PortfolioNav";
import { animateScroll as scroll } from "react-scroll";
import { useQuery } from "@apollo/react-hooks";
import { GetPortfolioQuery } from "../../data/ApiService";

export const scrollToRef = (ref, smooth = false) => {
  if (ref.current) {
    if (!smooth) {
      window.scrollTo(0, ref.current.offsetTop); // General scroll to element function
    } else {
      scroll.scrollTo(ref.current.offsetTop);
    }
  }
};

const PortfolioSection = () => {
  const [isInDetailsMode, setIsInDetailsMode] = useState(false);
  const [currentNavItemKey, setCurrentNavItemKey] = useState("");
  const { loading, error, data } = useQuery(GetPortfolioQuery);
  const portfolioList =
    data && data.portfolioItems ? data.portfolioItems : null;
  const sectionRef = useRef(null);
  const onDetailsClick = key => {
    setIsInDetailsMode(true);
    if (!currentNavItemKey) {
      setCurrentNavItemKey(key);
    }
  };
  const onDetailsClose = () => {
    setIsInDetailsMode(false);
  };
  const executeScroll = () => scrollToRef(sectionRef);
  const renderPortfolios = (portfolioItem, nextPortfolioItemKey) => {
    const key = `${portfolioItem.portfolioKey}`;
    return (
      <PortfolioItem
        key={key}
        nextKey={nextPortfolioItemKey}
        onNextClick={onNavItemClick}
        item={portfolioItem}
        onDetailsClick={onDetailsClick}
        onDetailsClose={onDetailsClose}
        currentNavItemKey={currentNavItemKey}
        executeScroll={executeScroll}
      />
    );
  };
  const onNavItemClick = key => {
    if (currentNavItemKey !== "") {
      let element: HTMLElement = document.querySelectorAll(
        `#closeItem${currentNavItemKey}`
      )[0] as HTMLElement;
      if (element) {
        element.click();
      }
    }
    let timeoutTime = currentNavItemKey ? 1500 : 200;
    setCurrentNavItemKey(key);
    setTimeout(() => {
      let element: HTMLElement = document.querySelectorAll(
        `#readMore${key}`
      )[0] as HTMLElement;
      element.click();
    }, timeoutTime);
  };
  const renderSubmenu = () => {
    return (
      <PortfolioNav
        onNavItemClick={onNavItemClick}
        portfolioList={data.portfolioItems}
      />
    );
  };

  return (
    <PortfolioSectionWrapper
      ref={sectionRef}
      className={`sectionWrapper ${isInDetailsMode ? "detailsMode" : ""}`}
    >
      {data && data.portfolioItems && (
        <>
          <StickyNavWithHeader
            color="lightNav"
            title="Work"
            sectionAnchor="portfolioSection"
            renderSubmenu={renderSubmenu()}
          />
          <div className="portfolio-section page">
            {data.portfolioItems.map((portfolioItem, index) => {
              const nextPorflioItemKey = data.portfolioItems[index + 1]
                ? data.portfolioItems[index + 1].portfolioKey
                : null;
              return renderPortfolios(portfolioItem, nextPorflioItemKey);
            })}
          </div>
        </>
      )}
    </PortfolioSectionWrapper>
  );
};
export default PortfolioSection;
