import { AppWrapper } from "./FinalStyles.style";
import React from "react";
import ResetStyles from "./ResetStyles";
import { Helmet } from "react-helmet";
import LandingPageHeader from "./components/LandingageHeader/LandingPageHeader";
import AboutSection from "./components/AboutSection/AboutSection";
import PortfolioSection from "./components/PortfolioSection/PortfolioSection";
import ContactSection from "./components/ContactSection/ContactSection";
import { ApolloProvider } from "@apollo/react-hooks";
import ApolloClient from "apollo-boost";

export const client = new ApolloClient({
  uri: "https://api-euwest.graphcms.com/v1/ck367equc0d4901dnevvkei4w/master"
});
const App = (): JSX.Element => {
  return (
    <ResetStyles>
      <ApolloProvider client={client}>
        <AppWrapper>
          <Helmet>
            <title>
              Jessica M. Sutherland Director of Marketing Charlotte - Portfolio
            </title>
            <meta
              name="description"
              content="Jessica M. Sutherland - focused and passionate professional with over 10 years of
          marketing experience living in Charlotte, North Carolina. "
            />
          </Helmet>
          <LandingPageHeader />
          <AboutSection />
          <PortfolioSection />
          <ContactSection />
        </AppWrapper>
      </ApolloProvider>
    </ResetStyles>
  );
};
export default App;
