import styled from "styled-components";
const popColor1 = "#e0b312";
export const AppWrapper = styled.div`
  .contentBody a {
    color: #000;
    border-bottom: 2px solid #e0b312;
    padding-bottom: 5px;
    &:hover {
      border-bottom: 4px solid ${popColor1};
      font-weight: 600;
      text-decoration: none;
    }
  }
  .contentWrapper a {
    color: #000;
    border-bottom: 2px solid ${popColor1};
    padding-bottom: 5px;
    &.iconLink {
      display: inline-block;
      margin-top: 90px;
      border-bottom: 0;
      &:hover {
        color: ${popColor1};
      }
    }
    &:hover {
      border-bottom: 4px solid ${popColor1};
      text-decoration: none;
    }
  }
  h3 {
    padding: 0 0 1.2em;
  }
  h5 {
    padding: 0 0 1.2em;
  }
  .page {
    min-height: 100vh;
    display: grid;
  }
  .sectionWrapper {
    display: flex;
    nav,
    .page {
      flex: 1;
      transition: all 0.5s;
    }
  }
  .contentWrapper {
    min-height: 100vh;
    padding: 96px;
    position: relative;
  }
  .clearfix {
    clear: both;
  }
  .noBullets {
    padding: 0;
    list-style: none;
  }
  .sideBySideUl {
    padding: 0 0 0 20px;
    width: 50%;
    float: left;
    display: block;
  }
  @media only screen and (max-width: 980px) {
    .sideBySideUl {
      width: 100%;
      clear: both;
      float: none;
    }
    .smallHeader {
      font-size: 12px !important;
    }
    .sectionWrapper {
      flex-direction: column;
    }
    .homeTitle {
      // margin-top: 90px !important;
      // font-size: 80px !important;
    }
    .mobileNavigation.active {
      font-size: 80px !important;
    }

    .contentWrapper {
      padding: 50px;
    }
    .navHeader h1 {
    }
    .contentSection {
      padding: 50px !important;
    }
  }
`;
